@import '../../sass/variables';

.error_wrapper{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 60px;
    height: 100vh;
    background-image: url('../../assets/DSCF3361.webp');
    background-size: cover;
    background-repeat: no-repeat;
}

.error_backToProjects{
    margin-top: $header_height;
}

.error_backToProjects > a{
    text-decoration: none;
    font-size: 40px;
}

.loading {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 60px;
    height: 100vh;
}